<template>
	<view class="container">
		<!-- <u-navbar :is-back="false" title=" " :background="background" title-color='#fff'></u-navbar> -->
		<view class="fixedTop">
			<u-tabs :list="list" :is-scroll="false" :current="current" @change="change" height="110" bg-color="#03958C" inactive-color="#fff" active-color="#fff" :active-item-style="activeStyle"></u-tabs>
		</view>
		<view class="customer" v-if="isshow">
			<view v-if="current==0" class="examine_box leftExamine">
				<view class="fixedSerarch">
					<view class="search_box">
						<u-search height="68" v-model="postData.searchCond" @search="searchTap" @custom="searchTap" :show-action="false" search-icon-color="#999"></u-search>
					</view>
					<selectBox :groups="groups" :intentionList="intentionLevels" :statusList="states" @groupsTap="_groupsTap" @selectTap="_selectTap"
					 @sureTap="_sureTap"></selectBox>
				</view>
				<customerList :isCustomer="'true'" :cusList="cusList"></customerList>
				<view class="regBtn" @click="goReg" :style="{backgroundImage:`url(${imageUrl})`}"></view>
			</view>
			<view v-if="current==1" class="examine_box queExam">
				<cusQuestion :cusList="queCusList"></cusQuestion>
			</view>
		</view>
		<tabBar></tabBar>
		<watermark></watermark>
	</view>
</template>

<script>
	import watermark from '@/components/watermark.vue';
	import tabBar from '@/components/tabBar3.vue';
	import selectBox from '@/pages/salemanVisual/customer/components/select_box.vue';
	import customerList from '@/pages/salemanVisual/customer/components/customerList.vue';
	import cusQuestion from '@/pages/salemanVisual/customer/components/cusQuestion.vue';
	import register from '@/static/salePerson/register.png';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		data() {
			return {
				background: {
					backgroundColor: '#03958C',
				},
				activeStyle: {
					fontSize:'36rpx'
				},
				isshow: false,
				imageUrl: register,
				list: [{
					name: '我的客户'
				}, {
					name: '问卷调查'
				}],
				current: 0,
				groups: [], // 筛选-分组list
				intentionLevels: [], // 筛选-意向级别list
				states: [], // 客户状态-分组list
				postData: { // 请求参数
					searchCond: '', // 查询条件
					sortCond: '', // 排序
					groupCond: '', // 分组
					screenCond1: '', // 客户状态
					screenCond2: '', // 意向级别
				},
				cusList: [],
				etype: 0, //0第一次加载 1查询 2下拉刷新 3上拉加载更多
				page: {
					pageNum: 1,
					pageSize: 10
				},
				totalPages: 0, // 总页数
				loading: false,
				// 以下是问卷客户列表
				queCusList: [],
				etype2: 0, //0第一次加载 1查询 2下拉刷新 3上拉加载更多
				page2: {
					pageNum: 1,
					pageSize: 10
				},
				totalPages2: 0, // 总页数
				loading2: false,
			}
		},
		onLoad(options) {
			this.current = options.current ? options.current: 0
			this._getSelectList()
			this._getList()
			// this._getQueList()
		},
		methods: {
			// 获取分组、筛选列表
			_getSelectList() {
				let that = this
				httpUtils.sendHttpRequest('2011', Object.assign({}, ''), function(res) {
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						that.groups = res.data.data.groups
						that.intentionLevels = res.data.data.intentionLevels
						that.states = res.data.data.states
					}
				})
			},
			// 分组回调
			_groupsTap(val) {
				this.postData.groupCond = val
				this.page.pageNum = 1
				this.etype = 0
				this._getList()
			},
			// 排序回调
			_selectTap(val) {
				this.postData.sortCond = val
				this.page.pageNum = 1
				this.etype = 0
				this._getList()
			},
			// 更多筛选回调
			_sureTap(val, val2) {
				this.postData.screenCond1 = val.join(',')
				this.postData.screenCond2 = val2.join(',')
				this.page.pageNum = 1
				this.etype = 0
				this._getList()
			},
			// 点击搜索
			searchTap(){
				this.page.pageNum = 1
				this.etype = 0
				this._getList()
			},
			// 获取客户列表
			_getList() {
				let that = this
				Tools.showLoading('加载中')
				httpUtils.sendHttpRequest('2012', Object.assign({}, that.postData, that.page), function(res) {
					Tools.hideLoading()
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						that.isshow = true
						that.cusList = that.etype == 3 ? that.cusList.concat(res.data.data) : res.data.data
						that.totalPages = res.data.totalPages
						if (that.loading) {
							that.loading = false
							uni.stopPullDownRefresh()
						}
					}
				})
			},
			// 获取问卷客户列表
			_getQueList(){
				let that = this
				httpUtils.sendHttpRequest('2044', Object.assign({}, that.page2), function(res) {
					uni.hideNavigationBarLoading()
					if (res.data.code == '0') {
						that.isshow = true
						that.queCusList = that.etype == 3 ? that.queCusList.concat(res.data.data) : res.data.data
						that.totalPages2 = res.data.totalPages
						if (that.loading2) {
							that.loading2 = false
							uni.stopPullDownRefresh()
						}
					}
				})
			},
			// 下拉刷新
			onPullDownRefresh() {
				if(this.current == 0){
					this.page.pageNum = 1
					this.etype = 2
					if (!this.loading) {
						this.loading = true
						this._getList()
					}
				}else{
					this.page2.pageNum = 1
					this.etype2 = 2
					if (!this.loading2) {
						this.loading2 = true
						this._getQueList()
					}
				}
			},
			// 上拉加载
			onReachBottom() {
				if (this.current == 0) {
					if (this.totalPages > this.page.pageNum) {
						this.etype = 3
						this.page.pageNum++
						this._getList()
					}
				} else {
					if (this.totalPages2 > this.page2.pageNum) {
						this.etype2 = 3
						this.page2.pageNum++
						this._getQueList()
					}
				}
			},
			// 提交问卷后，刷新当前页面
			_refresh(){
				this.page2.pageNum = 1
				this.queCusList = []
				this._getQueList()
			},
			// 新增登记后，刷新当前页面
			_refreshList(){
				this.page.pageNum = 1
				this.cusList = []
				this._getList()
			},
			// 点击切换
			change(index) {
				this.current = index;
				if (index == 0) {
					this.page.pageNum = 1
					this.cusList = []
					this._getList()
				} else {
					this.page2.pageNum = 1
					this.queCusList = []
					this._getQueList()
				}
			},
			// 跳转至登记
			goReg() {
				uni.navigateTo({
					url: '/pages/salemanVisual/customer/register'
				})
			},
		},
		components: {
			tabBar,
			selectBox,
			customerList,
			cusQuestion,
			watermark
		}
	}
</script>

<style lang="scss">
	page {
		height: 100%;
		background-color: #f6f6f6;
	}

	.container {
		.fixedTop{
			border: none!important;
			// .u-tabs{
			// 	// background: #03958C!important;
			// }
		}
		.customer {
			// padding-top: 270rpx;
			.fixedSerarch{
				position: fixed;
				top: 100rpx;
				width: 100%;
				background-color: #fff;
				.search_box{
					padding: 20rpx 30rpx;
				}
			}
			// h5持有样式
			/* #ifdef H5 */
			.fixedSerarch{
				top: 86px;
				.search_box{
					margin-top: 8px;
				}
			}
			/* #endif */
			.u-tabs {
				border-bottom: 20rpx solid #f6f6f6;
			}

			.search_box {
				background: #03958C;
				padding: 30rpx;
				border-bottom: 1rpx solid #eee;
			}
			.leftExamine{
				padding: 270rpx 0 0 0!important;
			}
			.queExam{
				padding: 100rpx 0 0 0!important;
			}
			.examine_box {
				text-align: center;
				padding: 30rpx;

				.examineList {
					display: flex;
					flex-direction: column;
					align-items: center;
					padding: 30rpx;
					border-bottom: 1rpx solid #f6f6f6;

					text {
						font-size: 32rpx;
					}

					image {
						width: 200rpx;
						height: 200rpx;
						background-color: #eee;
						margin: 30rpx 0;
					}

					.custom-btn {
						width: 500rpx;
						height: 70rpx;
						line-height: 70rpx;
						text-align: center;
						background-color: #03958C;
						color: #fff;
						border-radius: 6rpx;
						font-size: 32rpx;
					}
				}

				.regBtn {
					width: 120rpx;
					height: 120rpx;
					position: fixed;
					right: 30rpx;
					bottom: 180rpx;
					z-index: 99;
					background-size: cover;
				}
			}
		}
	}
</style>
